<template lang="html">
  <section class="person">
    <h1>{{ movieName }}</h1>
    <!-- PERSON IMG -->
    <v-img v-for="movie in movies" v-if="movie.profile_path" height="450" width="300" :src="`https://image.tmdb.org/t/p/w300_and_h450_bestv2${movie.profile_path}`" class="mx-auto my-10"></v-img>
    <v-img v-for="movie in movies" v-if="!movie.profile_path" height="450" width="300" src="../assets/noPhotoAvailable.png" class="mx-auto my-10"></v-img>
    <!-- ALERT -->
    <v-alert v-if="error" :dismissible="false" :type="'error'" class="mb-4 mt-12" height="50">{{this.errorMessage}}</v-alert>
    <!-- BIOGRAPHY -->
    <v-expansion-panels class="accordion">
      <v-expansion-panel class="text-left">
        <v-expansion-panel-header>Biography</v-expansion-panel-header>
        <v-expansion-panel-content v-for="movie in movies">{{ movie.biography }}</v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
    <!-- KNOWN FOR -->
    <h3 v-if="!error2" class="display-2 mt-3">Known for</h3>   
    <!-- ALERT2 -->
    <v-alert v-if="error2" :dismissible="false" :type="'error'" class="mb-4 mt-12" height="50">{{this.errorMessage2}}</v-alert>
    <v-flex v-for="films in nownFor" row justify-center justify-lg-start class="knownFor">
      <v-card v-for="person in films.cast" v-if="person.character" width="138" height="250" outlined class="mx-4 my-4 films" @click="changeHashAndOpenMovie(person.id, person.original_title)">
        <!-- IMAGE -->
        <v-img v-if="person.poster_path" height="175" width="138" :src="`https://image.tmdb.org/t/p/w138_and_h175_bestv2${person.poster_path}`"></v-img>
        <v-img v-if="!person.poster_path" height="175" width="138" src="../assets/noImageAvailable.jpg"></v-img>
        <!-- TEXT -->
        <v-card-title class="subtitle-2 text-left my-n3 ml-n2">{{person.character}}</v-card-title>
      </v-card>
    </v-flex>
  </section>

</template>

<script lang="js">
  import axios from 'axios'; // del
  import { mapState } from "vuex";
  export default  {
    name: 'movie',
    props: [],
    mounted() {
      const api_key = 'a6c11d39878e78754373ebc099d78d70';
      const categorie = this.$route.params.categorie.split('-')[0];
      const id = this.$route.params.id;
      const language = this.$route.params.language;
      const api_url = `https://api.themoviedb.org/3/${categorie}/${id}/movie_credits?api_key=${api_key}&language=${language}&region=US`;
      axios.get(api_url)
      .then((response) => {
        this.nownFor.push(response.data);
        console.log(response.data)
      }, (error) => {
        this.$store.dispatch('getError', {error:true, errorMessage:error});
        console.log(error)
      })
      this.$store.dispatch('removeData');
      this.$store.dispatch('getJSON', {categorie: categorie, id:id, page: 0, language: language});
    },
    data() {
      return {
        nownFor: [],
      }
    },
    methods: {
      changeHashAndOpenMovie(id, title) {
        title = title.replace(/ /g, '-').toLowerCase();
        this.$store.dispatch('removeData');
        this.$router.push({path:`/movie/movie-popular;${title};${id};${this.language};`});
      }
    },
    computed: {
      ...mapState([
        'movies',
        'currentCategorie',
        'language',
        'error',
        'errorMessage',
        'error2',
        'errorMessage2',
        'loader',
      ]),
      movieName: function () {
        return this.$route.params.name.replace(/-/g, ' ');
      }
    }
}
</script>

