<template lang="html">

  <section class="appFooter">
    <img class="sman" :class=" { active: smanActive }" src="../assets/sman.png" @click="$vuetify.goTo(target, options), test()"></img>
    <img class="smanfly" :class=" { active: smanFlyActive }" src="../assets/smanfly.png"></img>
  </section>

</template>

<script lang="js">
  export default  {
    name: 'app-footer',
    props: [],
    mounted() {},
    data() {
      return {
        duration: 2000,
        offset: 0,
        easing: 'linear',
        smanFlyActive: false,
        smanActive: false,
      }
    },
    methods: {
      test() {
        this.smanActive = true;
        this.smanFlyActive = true;
        setTimeout(() => {
          this.smanActive = false;
          this.smanFlyActive = false;
        }, 3000);
      }
    },
    computed: {
      target () {
        const value = 10;
        return value
      },
      options () {
        return {
          duration: this.duration,
          offset: this.offset,
          easing: this.easing,
        }
      }
    }
}
</script>

<style scoped lang="scss">
  .appFooter {
    height: 300px;
    background: lightslategrey;
    position: relative;
    .sman,.smanfly {
      position: absolute;
      right: 50px;
      top: -45px;
      height: 100px;
      width: 80px;
    }
    .sman {
      cursor: pointer;
      animation: sman 2s ease infinite;
      &.active {
        display: none;
      }
    }
    .smanfly {
      top: -300px;
      display: none;
      animation: smanfly 9s ease;
      &.active {
        display: block;
      }
    }
    @keyframes sman {
      0% {
        transform: none;
      } 
      50% {
        transform: translateY(-10px);
      }
      100% {
        transform: none;
      }
    }
    @keyframes smanfly {
      0% {
        transform: none;
      } 
      100% {
        transform: translateY(-10000px);
      }
    }
  }
</style>
