<template lang="html">
  <section class="popularTVshows">
    <h1>Popular TV</h1>
    <cards></cards>
  </section>
</template>
<script lang="js">
  import cards from './cards';
  export default  {
    name: 'popularTVshows',
    components: {cards},
    props: [],
    mounted() {
      this.$store.dispatch('removeData');
      this.$store.dispatch('getJSON', {categorie:'tv/popular',id:'', page: this.$route.params.page, language: this.$route.params.language})
    },
    data() {return {page: 1}},
}
</script>
