<template lang="html">
  <section class="searchPeople">
    <h1>search in people</h1>
    <cards></cards>
  </section>
</template>
<script lang="js">
  import cards from './cards';
  export default  {
    name: 'searchPeople',
    components: {cards},
    props: [],
    mounted() {
      const searchURL = `https://api.themoviedb.org/3/search/person?api_key=a6c11d39878e78754373ebc099d78d70&query=${this.$route.params.search}&page=${this.$route.params.page}`;
      this.$store.dispatch('removeData');
      this.$store.dispatch('getJSON', {categorie:'person/search', page: this.$route.params.page, language: this.$route.params.language, searchURL: searchURL});
    },
    data() {return {page: 1}},
  }
</script>
