import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import { store } from './store';
import popularMovies from './components/popularMovies.vue';
import topRatedMovies from './components/topRatedMovies.vue';
import upcomingMovies from './components/upcomingMovies.vue';
import popularTVshows from './components/popularTVshows.vue';
import topRatedTVshows from './components/topRatedTVshows.vue';
import popularPeople from './components/popularPeople.vue';
import searchMovies from './components/searchMovies.vue';
import searchPeople from './components/searchPeople.vue';
import searchByYear from './components/searchByYear.vue';
import movie from './components/movie.vue';
import person from './components/person.vue';
import home from './components/home.vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);
Vue.config.productionTip = false

const router = new VueRouter({
  routes: [
    { path: '*/', component: home },
    { path: '/movie/popular;page=:page;:language;', component: popularMovies },
    { path: '/movie/top_rated;page=:page;:language;', component: topRatedMovies },
    { path: '/movie/upcoming;page=:page;:language;', component: upcomingMovies },
    { path: '/tv/popular;page=:page;:language;', component: popularTVshows },
    { path: '/tv/top_rated;page=:page;:language;', component: topRatedTVshows },
    { path: '/person/popular;page=:page;:language;', component: popularPeople },
    { path: '/movie/:categorie;:name;:id;:language;', component: movie },
    { path: '/person/:categorie;:name;:id;:language;', component: person },
    { path: '/movie/search=:search;page=:page;:language;', component: searchMovies },
    { path: '/person/search=:search;page=:page;:language;', component: searchPeople },
    { path: '/movie/year=:year;page=:page;:language;', component: searchByYear },
  ],
  mode: 'history',
});

new Vue({
  vuetify,
  store,
  router,
  render: h => h(App)
}).$mount('#app')
