<template lang="html">
  <section class="movie">
    <h1>{{ movieName }}</h1>
    <!-- ALERT -->
    <v-alert v-if="error" :dismissible="false" :type="'error'" class="mb-4 mt-12" height="50">{{this.errorMessage}}</v-alert>
    <!-- BACKGROUND -->
    <div v-for="movie in movies" v-if="movie.backdrop_path" class="background" :style="`background-image:url(https://image.tmdb.org/t/p/original${movie.backdrop_path})`"></div>
    <div v-for="movie in movies" v-if="!movie.backdrop_path" class="background" style="../assets/noImageAvailable.jpg"></div>
    <div class="background-overlay" v-if="!error"></div>
    <v-flex v-if="this.currentCategorie !== 'person/popular'">
      <v-card v-for="movie in movies" width="900" height="470" outlined class="main-card">
        <v-layout flex-row class="layout">
          <v-flex class="ml-7 main_image_wrapper">
            <!-- IMAGE -->
            <v-img v-if="movie.poster_path" class="main_image" height="450" width="300" :src="`https://image.tmdb.org/t/p/w300_and_h450_bestv2${movie.poster_path}`"></v-img>
            <v-img v-if="!movie.poster_path" class="main_image" height="450" width="300" src="../assets/noImageAvailable.jpg"></v-img>
          </v-flex>
          <v-flex class="ml-12 about-movie">
            <v-flex row justify-start> 
              <!-- PROGRESS -->
              <div v-if="movie.vote_average*10 >= 0">
                <v-progress-circular v-if="movie.vote_average*10 >= 70" :rotate="-90" :size="150" :width="15" :value="movie.vote_average*10"  color="green">{{ movie.vote_average*10 }}</v-progress-circular>
                <v-progress-circular v-if="movie.vote_average*10 >= 50 && movie.vote_average*10 < 70" :rotate="-90" :size="150" :width="15" :value="movie.vote_average*10" color="orange">{{ movie.vote_average*10 }}</v-progress-circular>
                <v-progress-circular v-if="movie.vote_average*10 < 50" :rotate="-90" :size="150" :width="15" :value="movie.vote_average*10"  color="red">{{ movie.vote_average*10 }}</v-progress-circular>
              </div>
              <div>
                <!-- TEXT -->
                <v-card-title class="text-color">Release date</v-card-title>
                <v-card-text v-if="movie.release_date" class="text-left text-color">{{ movie.release_date }}</v-card-text>
                <v-card-text v-if="movie.first_air_date" class="text-left text-color">{{ movie.first_air_date }}</v-card-text>
                <v-card-title class="text-color mt-n6">Overview</v-card-title>
                <v-card-text class="text-left text-color">{{ movie.overview }}</v-card-text>
              </div>
            </v-flex>
          </v-flex>
        </v-layout>
      </v-card>
    </v-flex>
    <!-- VIDEO -->
    <v-carousel v-for="movie in movies" :key="movie.id" :hide-delimiters="true" class="mt-n4">
      <v-carousel-item v-if="movie.videos.results" v-for="link in movie.videos.results">
        <v-sheet :movie="movie">
          <v-row class="fill-height mt-n7" align="center" justify="center">
            <iframe class="videos" :src="`https://www.youtube.com/embed/${link.key}`" allow="camera;microphone" allowfullscreen="allowfullscreen"></iframe>
          </v-row>
        </v-sheet>
      </v-carousel-item>

      <v-carousel-item v-if="movie.videos.results.length == 0 || !movie.videos || !movie.videos.results">
        <v-sheet>
          <v-row class="fill-height mt-n7" align="center" justify="center">
            <iframe class="videos" src="https://www.youtube.com/embed/ARM42-eorzE" allow="camera;microphone" allowfullscreen="allowfullscreen"></iframe>
          </v-row>
        </v-sheet>
      </v-carousel-item>
    </v-carousel>
    <!-- CAST AND CREW -->
    <h3 v-if="!error" class="display-2 mt-3 crew-header-text">Full Cast & Crew</h3>    
    <v-flex v-for="movie in movies" row justify-center justify-lg-start class="crew">
      <v-card v-for="preson in movie.credits.cast" width="138" height="260" outlined class="mx-4 my-4 person" @click="changeHashAndOpenPerson(preson.id, preson.name)">
        <!-- IMAGE -->
        <v-img v-if="preson.profile_path" height="175" width="138" :src="`https://image.tmdb.org/t/p/w138_and_h175_bestv2${preson.profile_path}`"></v-img>
        <v-img v-if="!preson.profile_path" height="175" width="138" src="../assets/images.png"></v-img>
        <!-- TEXT -->
        <v-card-title class="subtitle-2 text-left my-n3 ml-n2">{{ preson.name }}</v-card-title>
        <v-card-text class="text-left ml-n2 caption">{{ preson.character }}</v-card-text>
      </v-card>
    </v-flex>
  </section>
</template>

<script lang="js">
  import { mapState } from "vuex";
  export default  {
    name: 'movie',
    props: [],
    mounted() {
      this.$store.dispatch('removeData');
      this.$store.dispatch('getJSON', {categorie: this.$route.params.categorie.split('-')[0], id:this.$route.params.id, page: 0, language: this.$route.params.language});
    },
    data() {return {}},
    methods: {
      changeHashAndOpenPerson(id, name) {
        name = name.replace(/ /g, '-').toLowerCase();
        this.$store.dispatch('removeData');
        this.$router.push({path:`/person/person-popular;${name};${id};${this.language};`});
      }
    },
    computed: {
      ...mapState(['movies','currentCategorie','language','error','errorMessage','loader',]),
      movieName: function () {
        return this.$route.params.name.replace(/-/g, ' ');
      }
    }
}
</script>

