<template lang="html">
  <section class="appHeader">
    <!-- menu -->
    <v-app-bar-nav-icon @click.stop="drawer = !drawer" absolute left color="rgb(22, 100, 190)"></v-app-bar-nav-icon>
      <v-navigation-drawer v-model="drawer" absolute left temporary deep-purple accent-4 color="lightslategrey">
        <v-list nav>
          <v-list-item-group v-model="group" active-class="blue--text text--accent-4">
            <router-link :to="`${item.link}`" v-for="item in menu">
              <v-list-item><v-list-item-title>{{ item.name }}</v-list-item-title></v-list-item>
            </router-link>
          </v-list-item-group>
        </v-list>
      </v-navigation-drawer>
    <!-- LANGUAGE -->
    <v-btn class="lang-btn ru" outlined @click="changeLanguage('ru')"><img src="../assets/ru.png" alt="" class="lang-img"></v-btn>
    <v-btn class="lang-btn us" outlined @click="changeLanguage('en-US')"><img src="../assets/usa.png" alt="" class="lang-img"></img></v-btn>
    <!-- SEARCH -->
    <v-dialog fullscreen hide-overlay transition="dialog-bottom-transition" v-model="dialog">
      <template v-slot:activator="{ on }">
        <v-btn icon class="search-btn" height="150" width="150" @click.stop="dialog = true"><v-img src="../assets/search.png" height="150" width="150"></v-img></v-btn>
      </template>
      <v-card>
        <v-toolbar color="primary">
        <!-- <v-toolbar color="#778899"> -->
          <v-btn icon dark @click="dialog = false" class="search-close-btn">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <!-- SEARCH FIELD -->
        <div class="search-type-wrapper" v-if="switchOverlay">
          <p class="display-1">Search by search field</p>
          <v-row align="center" justify="center">
            <v-radio-group row v-model="radios" >
              <v-radio label="in movies" value="movies" color="primary" class="mx-5"></v-radio>
              <v-radio label="in people" value="people" color="primary" class="mx-5"></v-radio>
            </v-radio-group>
          </v-row>
          <v-toolbar dense floating class="search-field">
            <v-text-field hide-details single-line v-model="searchInpVal"></v-text-field>
            <v-btn icon @click="startSearching('field')">
              <v-img src="../assets/search.png" height="50" width="50"></v-img>
            </v-btn>
          </v-toolbar>
        </div>
        <!-- SWITCH -->
        <p class="overline sw-search-type">Change search type</p>
        <v-row align="center" justify="center" class="switch">
          <v-switch color="primary" v-model="switchOverlay"></v-switch>
        </v-row>
        <!-- YEARS -->
        <div class="search-type-wrapper" v-if="!switchOverlay">
          <p class="display-1">Search by year</p>
          <v-select :items="years" label="Year" v-model="currentYear" class="years-select"></v-select>
          <v-btn color="primary" class="mt-2" @click="startSearching('year')">search</v-btn>
        </div>
      </v-card>
    </v-dialog>
  </section>
</template>

<script lang="js">
  import { mapState } from "vuex";
  export default  {
    props: [],
    mounted() {
    },
    data() {
      return {
        drawer: false,
        group: null,
        currentYear: 2019,
        dialog: false,
        radios: 'movies',
        switchOverlay: true,
        searchInpVal: '',
        menu: [
          {link:`/movie/popular;page=${1};${'en-US'};`, name:'Popular movies'},
          {link:`/movie/top_rated;page=${1};${'en-US'};`, name:'Top rated movies'},
          {link:`/movie/upcoming;page=${1};${'en-US'};`, name:'Upcoming movies'},
          {link:`/tv/popular;page=${1};${'en-US'};`, name:'Popular TV shows'},
          {link:`/tv/top_rated;page=${1};${'en-US'};`, name:'Top rated TV shows'},
          {link:`/person/popular;page=${1};${'en-US'};`, name:'Popular people'},
        ],
        years: [
          2020, 2019, 2018, 2017, 2016, 2015, 2014, 2013, 2012, 2011, 
          2010, 2009, 2008, 2007, 2006, 2005, 2004, 2003, 2002, 2001, 
          2000, 1999, 1998, 1997, 1996, 1995, 1994, 1993, 1992, 1991, 
          1990, 1989, 1988, 1987, 1986, 1985, 1984, 1983, 1982, 1981,
          1980, 1979, 1978, 1977, 1976, 1975, 1974, 1973, 1972, 1971,
          1970, 1969, 1968, 1967, 1966, 1965, 1964, 1963, 1962, 1961,
          1960, 1959, 1958, 1957, 1956, 1955, 1954, 1953, 1952, 1951,
          1950, 1949, 1948, 1947, 1946, 1945, 1944, 1943, 1942, 1941,
          1940, 1939, 1938, 1937, 1936, 1935, 1934, 1933, 1932, 1931,
          1930, 1929, 1928, 1927, 1926, 1925, 1924, 1923, 1922, 1921,
          1920, 1919, 1918, 1917, 1916, 1915, 1914, 1913, 1912, 1911,
          1910, 1909, 1908, 1907, 1906, 1905, 1904, 1903, 1902, 1901,
          1900
        ],
        input: '',
      }
    },
    methods: {
      changeLanguage(language) {
        if (this.$route.params.language !== language) { 
          if (!this.$route.params.page) {
            console.log(this.$route.params);
            console.log(this.$route.path.split(';')[0]);
            this.$router.push({ path:`${this.$route.path.split(';')[0]};${this.$route.params.name};${this.$route.params.id};${language};`});
            this.$store.dispatch('removeData');
            this.$store.dispatch('getJSON', {categorie:`${this.currentCategorie}`, id:`${this.$route.params.id}`, page:'', language:`${language}`});
            location.reload();
          } else {
            this.$router.push({ path: `/${this.currentCategorie};page=${this.$route.params.page};${language};`});
            this.$store.dispatch('removeData');
            this.$store.dispatch('getJSON', {categorie:`${this.currentCategorie}`, id:'', page:`${this.$route.params.page}`, language:`${language}`});
            location.reload();
          }
        }
      },
      startSearching(type) {
        this.dialog = false;
        this.$store.dispatch('removeData');
        if(type === 'field') {
          if(this.searchInpVal !== '') {
            if(this.radios === 'movies') {
              // search in movies
              const searchURL = `https://api.themoviedb.org/3/search/movie?api_key=a6c11d39878e78754373ebc099d78d70&query=${this.searchInpVal}&page=${1}`;
              this.$router.push({path:`/movie/search=${this.searchInpVal};page=${1};${this.language};`});
              this.$store.dispatch('getJSON', {categorie:'movie/search', page: 1, language: this.language, searchURL: searchURL});
            } else {
              // search in people
              const searchURL = `https://api.themoviedb.org/3/search/person?api_key=a6c11d39878e78754373ebc099d78d70&query=${this.searchInpVal}&page=${1}`;
              this.$router.push({path:`/person/search=${this.searchInpVal};page=${1};${this.language};`});
              this.$store.dispatch('getJSON', {categorie:'person/search', page: 1, language: this.language, searchURL: searchURL});
            }
          }
        } else {
          // search by year
          const searchURL = `https://api.themoviedb.org/3/discover/movie?api_key=a6c11d39878e78754373ebc099d78d70&primary_release_year=${this.currentYear}&page=${1}`;
          this.$router.push({path:`/movie/year=${this.currentYear};page=${1};${this.language};`});
          this.$store.dispatch('getJSON', {categorie:'movie/year', page: 1, language: this.language, searchURL: searchURL});
        }
      },
    },
    computed: {...mapState(['currentCategorie','language',])},
  }
</script>

