<template lang="html">
  <section class="searchByYear">
    <h1>search by year</h1>
    <cards></cards>
  </section>
</template>
<script lang="js">
  import cards from './cards';
  export default  {
    name: 'searchByYear',
    components: {cards},
    props: [],
    mounted() {
      const searchURL = `https://api.themoviedb.org/3/discover/movie?api_key=a6c11d39878e78754373ebc099d78d70&primary_release_year=${this.$route.params.year}&page=${this.$route.params.page}`;
      this.$store.dispatch('removeData');
      this.$store.dispatch('getJSON', {categorie:'movie/year', page: this.$route.params.page, language: this.$route.params.language, searchURL: searchURL});
    },
    data() {return {page: 1}},
  }
</script>
