import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    movies: [],
    total_pages: 0,
    currentPage: 1,
    currentCategorie: '',
    language: '',
    error: false,
    error2: false,
    errorMessage: '',
    errorMessage2: '',
    loader: false,
  },
  getters: {
    GET_MOVIES: state => state.movies,
    GET_CATEGORIE: state => state.currentCategorie,
    GET_TOTAL_PAGES: state => state.total_pages,
  },
  mutations: {
    SET_MOVIES: (state, payload) => {
      state.movies.push(payload);
    },
    SET_CATEGORIE: (state, payload) => {
      state.currentCategorie = payload;
    },
    SET_LANGUAGE: (state, payload) => {
      state.language = payload;
    },
    SET_PAGE: (state, payload) => {
      state.currentPage = payload;
    },
    SET_ERROR: (state, payload) => {
      state.error = payload;
    },
    SET_ERROR2: (state, payload) => {
      state.error2 = payload;
    },
    SET_ERROR_MESSAGE: (state, payload) => {
      state.errorMessage = payload;
    },
    SET_ERROR_MESSAGE2: (state, payload) => {
      state.errorMessage2 = payload;
    },
    SET_TOTAL_PAGES: (state, payload) => {
      state.total_pages = payload;
    },
    SET_LOADER: (state, payload) => {
      state.loader = payload;
    },
    REMOVE_MOVIES: (state) => {
      state.movies.length = 0;
    },
  },
  actions: {
    getJSON(context, movieInfo) {
      context.commit('SET_CATEGORIE', movieInfo.categorie);
      context.commit('SET_PAGE', movieInfo.page);
      // loader
      context.commit('SET_LOADER', true);
      movieInfo.language == 'undefined' ? context.commit('SET_LANGUAGE', 'en-US') : context.commit('SET_LANGUAGE', movieInfo.language);
      const api_key = 'a6c11d39878e78754373ebc099d78d70';
      let api_url = '';
      if(movieInfo.searchURL) {
        api_url = movieInfo.searchURL;
      } else {
        if(movieInfo.id) {
          api_url = `https://api.themoviedb.org/3/${movieInfo.categorie}/${movieInfo.id}?api_key=${api_key}&language=${movieInfo.language}&region=US&append_to_response=videos,credits`;
        } else {
          api_url = `https://api.themoviedb.org/3/${movieInfo.categorie}?api_key=${api_key}&language=${movieInfo.language}&page=${movieInfo.page}&region=US`;
        }
      }
      axios.get(api_url)
      .then((response) => {
        // loader
        context.commit('SET_LOADER', false);
        context.commit('SET_ERROR', false);
        if(movieInfo.id) {
          context.commit('SET_MOVIES', response.data);
        } else {
          context.commit('SET_TOTAL_PAGES', response.data.total_pages);
          response.data.results.forEach(el => {
            context.commit('SET_MOVIES', el);
          });
        }
      }, (error) => {
        context.commit('SET_LOADER', false);
        context.commit('SET_ERROR', true);
        context.commit('SET_ERROR_MESSAGE', error);
        console.log(error)
      })
    },
    removeData(context) {
      context.commit('REMOVE_MOVIES');
    },
    getError(context, errorObj) {
      context.commit('SET_ERROR2', errorObj.error);
      context.commit('SET_ERROR_MESSAGE2', errorObj.errorMessage);
    },   
  },
});