<template lang="html">
  <section class="popularMovies">
    <h1>Popular Movies</h1>
    <cards></cards>
  </section>
</template>
<script lang="js">
  import cards from './cards';
  export default  {
    name: 'popularMovies',
    components: {cards},
    props: [],
    mounted() {
      this.$store.dispatch('removeData');
      this.$store.dispatch('getJSON', {categorie:'movie/popular',id:'', page: this.$route.params.page, language: this.$route.params.language});
    },
    data() {return {page: 1}},
  }
</script>
